<template>
  <div class="home">
    <el-row :gutter="10">
      <el-col :xs="24"><div class="banner">banner</div></el-col>
    </el-row>
    <el-row :gutter="10">
      <el-col :xs="24" :sm="12" ><div class="grid-content bg-purple">对对对</div></el-col>
      <el-col :xs="24" :sm="12" ><div class="grid-content bg-purple-light">对对对</div></el-col>
    </el-row>
  </div>
</template>

<script>
  import { httpGet } from '../../utils/index'
  export default {
    data () {
      return {
        input: '',
        keyword: '',
        blacks: [],
        showBlack: false,
        show: false,
        shadow_img: '',
        toggle: false,
        showNull: false,
        placeholder: '请输入要查询的手机号/QQ号/微信号/身份证号/姓名',
        flag: true,
        not: false,
        init: true
      }
    },
    mounted () {
//      this.IsPC()
//      this.blacksHot()
    },
    methods: {
      IsPC () {
        const that = this
        const userAgentInfo = navigator.userAgent
        const Agents = ['Android', 'iPhone',
          'SymbianOS', 'Windows Phone',
          'iPad', 'iPod']
        for (var v = 0; v < Agents.length; v++) {
          if (userAgentInfo.indexOf(Agents[v]) > 0) {
            that.flag = false
            break
          } else {
            that.flag = true
          }
        }
      },
      blacksSearch () {
        const that = this
        if (!that.keyword) {
          that.showNull = true
          that.blacks = []
          that.show = false
          return
        }
        httpGet('/blacks/search', {keyword: that.keyword}, res => {
          that.show = true
          that.init = false
          that.showNull = false
          if (res.data.error_code === 0) {
            that.blacks = res.data.blacks
            that.keyword = ''
            console.log(that.blacks)
          }
        })
      },
      magnify (img) {
        this.shadow_img = img
        this.toggle = true
      },
      clear () {
        this.shadow_img = ''
        this.toggle = false
      },
      focus () {
        this.placeholder = ''
        this.not = true
      },
      blur () {
        this.placeholder = '请输入要查询的手机号/QQ号/微信号/身份证号/姓名'
      },
      blacksHot () {
        const that = this
        httpGet('/blacks/hot', {}, res => {
          that.show = true
          that.init = true
          that.showNull = false
          if (res.data.error_code === 0) {
            that.blacks = res.data.blacks
          }
        })
      }
    }
  }
</script>

<style scoped>
  .home{
    padding-top: 60px;
  }
  .banner{
    background: #fff;
  }
</style>



