import {routerMode} from '../utils/base'
import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index/index'
import home from '@/pages/home/index'
import miss from '@/pages/404/index'
import disclose from '@/pages/disclose/index'
import help from '@/pages/help/index'

Vue.use(Router)

export default new Router({
  mode: routerMode,
  routes: [
    {
      path: '/',
      component: index,
      children: [
        { path: '/', component: home },
        { path: '/home', component: home },
        { path: '/disclose', component: disclose },
        { path: '/help', component: help }
      ]
    },
    { path: '*', component: miss }
  ]
})



